<style lang="less">
  #header {
    display: flex;
    height: 90px;
    position: absolute;
    top: 20px;
    left: 20px;
    div {
      flex: 10;
      padding-left: 20px;
      height: 90px;
      vertical-align: middle;
      img {
        width: 40px;
        height: 60px;
        vertical-align: middle;
      }
    }
  } // 头部
  #nav {
    width: 100%;
  }
  #nav>img {
    width: 100%;
    height: 100%;
  }
  /* 设置头部图片 END */
  /* 设置中部课程名称/报名人数/讲师 头像/讲师名称 START */
  .middle_course_title {
    width: 100%;
    display: -webkit-flex;
    display: flex;
  }
  .middle_course_title>div:nth-child(1) {
    -webkit-box-flex: 7;
    flex: 7;
    padding: 20px 0 0 20px;
  }
  .middle_course_title>div:nth-child(2) {
    -webkit-box-flex: 3;
    flex: 3;
  }
  .middle_course_title>div:nth-child(2)>img {
    width: 40%;
    float: left;
    border-radius: 20px;
    border: 3px solid #aaa;
    margin-top: 20px;
  }
  .middle_course_title>div:nth-child(2)>a {
    display: inline-block;
    padding: 25% 0 0 5%;
    color: #777;
  }
  /* 设置中部课程名称/报名人数/讲师 头像/讲师名称 END */
  /* 底部内容-->课程简介 START*/
  #article {
    margin-top: 20px;
    box-sizing: border-box;
    width: 100%;
    &>p:nth-child(2) {
      width: 100%;
      &>img {
        width: 100%;
      }
    }
    img {
      width: 100%;
    }
  }
  /* 底部内容-->课程简介 END */
  /* 设置底部我要报名 START */
  .iwillaplly{
    position: fixed;
    bottom: 100px;
    left: 50%;
    transform: translateX(-50%);
    background: -webkit-linear-gradient(#83c639, #45b340);
    background: -o-linear-gradient(#83c639, #45b340);
    background: -moz-linear-gradient(#83c639, #45b340);
    background: linear-gradient(#83c639, #45b340);
    width: 50%;
    height: 90px;
    border-radius: 1.25rem;
    color: #fff;
    border: none;
    outline: none;
  }
  /* 设置底部我要报名 END */
</style>

<template>
  <div>
    <div>
      <!-- 设置返回按钮 START-->
      <div class="header" id="header">
        <div><img :src="directIcon" alt="返回" class="back" @click="back"></div>
      </div>
      <!-- 设置返回按钮 END -->
      <!-- 设置头图 START -->
      <nav id="nav">
        <img :src="vipDetail.coverImage" alt="VIP课程详情页图片">
      </nav>
      <!-- 设置头图 END -->
      <!-- 设置中部课程名称/报名人数/讲师头像/讲师名称 START -->
      <section class="middle_course_title">
        <div>
          <h1>{{vipDetail.title}}</h1>
          <span>{{vipDetail.totalStudent | numFm}}人报名</span>
        </div>
        <div>
          <img :src="vipDetail.teacherAvatar && vipDetail.teacherAvatar.indexOf('http') == -1 ? productURL + vipDetail.teacherAvatar : vipDetail.teacherAvatar" alt="头像">
          <a >{{vipDetail.teacherName}}</a>
        </div>
      </section>
      <!-- 设置中部课程名称/报名人数/讲师 头像/讲师名称 END -->
      <!-- 底部课程简介内容区域 START -->
      <article id="article" v-html="vipDetail.vodContent"></article>
    </div>
    <button class="iwillaplly" @click="mark">我要报名</button>
    <transition enter-active-class="zoomIn" leave-active-class="zoomOut">
      <CourseInfo v-if="showCourseInfo" class="animated" :couseInfo="vipDetail" v-on:closeMarketModal="closeMarketModal"></CourseInfo>
    </transition>
  </div>
</template>
<script>
  // 课程信息
  import CourseInfo from '@/components/courseInfo'
  import { createNamespacedHelpers } from 'vuex'
  const { mapMutations, mapActions, mapState} = createNamespacedHelpers('homeModule')
  export default {
    components: {
      CourseInfo
    },
    data() {
      return {
        // Vip课程对象
        vipDetail: Object,
        // 返回ICON
        directIcon: require('../../../assets/img/per_fh.png'),
        // 显示课程信息
        showCourseInfo: 0
      }
    },
    created() {
      this.getSingleVipCourse()
    },
    methods: {
      //调用store中的action
      ...mapActions([
        'VIPDETAIL'
      ]),
      // 通过课程ID获取相关课程信息
      getSingleVipCourse(){
        this.VIPDETAIL({
          courseId: this.$route.params.id
        }).then( data => {
          this.vipDetail = data.data.result[0]
        }).catch(err => {
          console.log(err)
        })
      },
      back() {
        this.$router.go(-1);
      },
      // 点击按钮显示课程收费以及章节信息
      mark(){
        this.showCourseInfo = 1
      },
      // 点击关闭按钮隐藏课程收费以及章节信息
      closeMarketModal(){
        this.showCourseInfo = 0
      }
    }
  }
</script>



